body {
    margin: 0;
    font-family: 'Segoe UI', sans-serif;
    background-color: #121212;
    color: #fff;
  }
  
  .app-layout {
    display: flex;
  }
  
  .main-content {
    margin-left: 220px;
    padding: 1.5rem;
    flex: 1;
    background-color: #1a1a1a;
  }
  
  .dashboard-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 1.5rem;
    padding-top: 1rem;
  }
  
  input, button {
    background-color: #2a2a2a;
    color: #fff;
    border: 1px solid #444;
    padding: 0.6rem;
    border-radius: 8px;
    margin-top: 0.5rem;
  }
  
  h2, h3 {
    color: #90caf9;
  }
  
  canvas {
    width: 100%;
    max-width: 400px;
    border-radius: 8px;
  }
  